@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*  Home page style  */

/* Scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #252734;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffaf29;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #acadb2;
}

.homePageContainer {
  width: 100%;
}

/*  Footer  */

.footer {
  background-color: #252734;
  color: #ebebec;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: start;
}

.footerContent {
  width: 60%;
  display: flex;
  justify-content: space-between;
}

.firstColumnFooter {
  width: 50%;
}

.secondColumnFooter {
  width: 50%;
  text-align: end;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}

.footerIconsContainer {
  margin: 10px 0;
  display: flex;
  justify-content: space-around;
  width: 20%;
}

.iconFooter {
  transition: ease-in-out 100ms;
}

footer a .iconFooter:hover {
  color: #ffaf29;
}

.footerSeparator {
  width: 35px;
  height: 3px;
  border: none;
  background-color: #ffaf29;
  margin: 10px 10px;
}

.linkIconHeader:hover {
  transform: scale(1.1);
}
.linkIconHeader:hover .iconHeader {
  color: #ffaf29;
}

/* Responsive */
@media (max-width: 1060px) {
  .footer {
    flex-direction: column;
    height: 50vh;
  }

  .footerContent {
    flex-direction: column;
  }

  .firstColumnFooter {
    width: 100%;
  }

  .secondColumnFooter {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: start;
  }
  .secondColumnFooter p {
    width: 60%;
  }

  .footerIconsContainer {
    width: 50%;
  }
}

/*  Selected projects */

.projectOverview {
  display: flex;
  flex-direction: column;
  background-color: #252734;
  width: 100%;
  margin: 1rem 0;
  padding: 2rem 1.5rem;
  box-sizing: border-box;
  transition: ease 0.3s;
}

.projectOverviewDark {
  background-color: #424657;
}

.selectedProjects {
  margin-top: 25px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.selectedProjectsContent {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.firstColumnSelectedProjects {
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.projectsLastMonthsText {
  width: 300px;
}

.imageContainer {
  width: 100%;
  transition: ease-in-out 0.3s;
  height: 100%;
}

.projectImage {
  height: 100%;
}

.textLeft {
  padding-right: 1rem;
}

.textRight {
  padding-left: 1rem;
}

.projectTitle {
  margin: 0 0 0.2rem;
  width: 100%;
  text-align: start;
  color: #ebebec;
}

.underlineProjectTitle {
  background-color: #ffaf29;
  height: 0.2rem;
  width: 2rem;
}

@media (max-width: 1060px) {
  .projectOverview {
    flex-direction: column;
    height: auto;
  }
}

.projectTextContainer {
  width: 100%;
  padding: 1rem 0;
  color: #ebebec;
  text-align: start;
  font-family: "Open Sans", sans-serif;
  box-sizing: border-box;
}

.projectDescriptionContainer {
  width: 50%;
  padding-right: 1rem;
  box-sizing: border-box;
}

.projectTechsContainer {
  width: 25%;
  padding-right: 1rem;
  box-sizing: border-box;
}

.columnButtonsText {
  width: 20%;
  display: flex;
  flex-direction: column;
  width: 20%;
  align-items: flex-start;
}

@media (max-width: 1060px) {
  .projectDescriptionContainer,
  .projectTechsContainer,
  .columnButtonsText {
    width: 100%;
    padding: 0;
  }

  .projectDescriptionContainer,
  .projectTechsContainer {
    margin-bottom: 1rem;
  }

  .projectTextElementsContainers {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

/*  Projects  */

.projects {
  background-color: #333646;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 5rem;
}

.projectsContent {
  width: 60%;
}

.projectsSquareContainer {
  display: flex;
  margin-top: -130px;
  justify-content: space-between;
  width: 100%;
}

.projectsSquare {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 17rem;
  height: 17rem;
  background-color: #424657;
  color: #ebebec;
  box-sizing: border-box;
  padding: 45px 15px;
  font-family: "Open Sans", sans-serif;
  text-align: start;
}

.iconTextProjectSquare {
  display: flex;
  width: 200px;
}

.portfolioText h2 {
  margin: 0;
  color: #ebebec;
}

.projectSquareTitle {
  font-size: 36px;
  margin: 10px 0;
}

.projectSquareTextContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
}

.orangeSquare {
  background-color: #ffaf29;
  color: #fff7ea;
}

/* Responsive */

@media (max-width: 1060px) {
  .projects {
    height: auto;
    padding: 50px 25px;
    box-sizing: border-box;
  }

  .selectedProjects {
    height: auto;
  }

  .projectsLastMonthsText {
    width: auto;
  }

  .projectsContent {
    width: 80%;
  }

  .projectsSquareContainer {
    margin: 5px;
    flex-direction: column;
  }

  .projectsSquare {
    width: 100%;
    height: 220px;
    margin: 15px 0;
  }

  .selectedProjectsContent {
    flex-direction: column;
  }

  /*  Portfolio   */

  .projects {
    justify-content: initial;
  }
  .firstColumnSelectedProjects {
    width: 100%;
  }
}

.header {
  background-color: #333646;
  display: flex;
  justify-content: center;
  padding: 15rem 0;
  width: 100%;
}

.headerContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnOneHeader {
  color: #ebebec;
  width: 50%;
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.nameTitle {
  font-size: 66px;
  margin: -10px 0;
  font-weight: bold;
  letter-spacing: 0.2rem;
  text-shadow: 0 3px 9px #252734, 0 -2px 1px #fff;
}

.separatorHeader {
  border: none;
  height: 7px;
  width: 50px;
  background-color: #ffaf29;
  margin: 15px 0 0 0;
}

.iconContainer {
  display: flex;
  justify-content: flex-start;
  margin: 75px 0 0 0;
}

.iconHeader {
  transition: ease-in-out 0.2s;
}

.linkIconHeader:hover {
  transform: scale(1.1);
}
.linkIconHeader:hover .iconHeader {
  color: #ffaf29;
}

.columnTwoHeader {
  text-align: start;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 50%;
}

.introHeader {
  width: 60%;
  color: #acadb2;
  margin: 10px 0;
}

.subtitleHeader {
  margin: 0 0 15px 0;
  color: #ebebec;
  width: 60%;
}

.subtitleHeaderText {
  color: #acadb2;
  width: 60%;
}

@media (max-width: 1060px) {
  .header {
    height: auto;
    padding: 50px 25px;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
  }

  .reactIconHeader {
    display: none;
  }

  .headerContent {
    flex-direction: column;
    width: 80%;
  }

  .iconContainer {
    margin: 65px 0 0 0;
  }

  .columnOneHeader p {
    font-size: 46px;
  }

  .subtitleHeader {
    width: 100%;
  }

  .columnTwoHeader {
    margin-top: 25px;
    width: 100%;
    align-items: flex-start;
  }
}

/*  About section   */

.aboutContainer {
  background-color: #252734;
  width: 100%;
  padding: 15rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.aboutContent {
  width: 60%;
  display: flex;
  justify-content: center;
}

.columnAboutContainer {
  display: flex;
  text-align: start;
  justify-content: center;
}

.firstColumnAbout {
  width: 50%;
}

.secColumnAbout {
  width: 50%;
}

.expProjectsContainer {
  display: flex;
  align-items: flex-end;
  height: 80px;
}

.numberExpProject {
  color: #ffaf29;
  font-size: 58px;
  margin: 0;
}

.textExpProject {
  color: #ebebec;
  margin: 0 30px 15px 10px;
}

@media (max-width: 1060px) {
  .aboutContainer {
    height: auto;
    padding: 50px 25px;
    box-sizing: border-box;
  }

  .aboutContent {
    flex-direction: column;
    width: 80%;
  }

  .columnAboutContainer {
    flex-direction: column;
  }

  .firstColumnAbout {
    width: 100%;
  }

  .secColumnAbout {
    width: 100%;
  }

  .numberExpProject {
    font-size: 42px;
  }

  .textExpProject {
    margin: 5px;
  }
}

